body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.dropdown-1 {
  width: 500px;
}

img {
  max-width: 100%;
  width: auto;
  height: auto;
}

body {
  background-image: url(/static/media/background.a5f0ac56.jpg);
  background-color: #cccccc;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
  position: absolute;

  width: 100%;
  height: 100%;
}

.lip-container {
  text-align: center;
  position: absolute;
  top: 40%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  color: white;
  /*overflow-y: scroll*/
  /*background-color: grey;*/
  /*opacity: 0.6*/
}

.lip-title {
  background-image: url(/static/media/title.28f4ada6.png);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: absolute;
  width: 150%;
  height: 150%;
}

.dropdown-group {
  text-align: center;
  position: center;
}

.output-container {
  text-align: center;
  position: absolute;
  top: 40%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  color: white;
}

CharacteristicInput {
  margin: 0 auto;
}

BrandInput {
  margin: 0 auto;
}


.form-text {
  text-shadow: 2px 2px 2px black;
  -webkit-text-fill-color: white; /* Will override color (regardless of order) */
  -webkit-text-stroke-width: 1px;
  -webkit-text-stroke-color: white;
}

.dropdown-toggle {
  width: 200px;
}

.dropdown-menu {
  height: 200px;
  overflow-y: scroll;
}

.dropdown-item {
  width: 230px;
}

.cd-fixed-bg {
  min-height: 100vh;
  background-size: cover;
  background-attachment: fixed;
  background-repeat: repeat;
  background-position: center center;
}

.cd-fixed-bg.cd-fixed-bg--1 {
  background-image: url(/static/media/background.a5f0ac56.jpg);
}

.cd-scrolling-bg {
  min-height: 100%;
}


OutputContainer {
    max-width: 80%;
    text-align: center;
}

.output-parent {
    text-align: center;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    /*padding: 20px;*/
    max-width: 100%;
    /*left: 13%;*/
    margin-bottom: 50px;
    /*transform: translate(-50%, -50%);*/
    overflow-y: scroll
    /*background-color: grey;*/
    /*opacity: 0.6*/
}

.card-deck {
    text-align: center;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 20px;
}

.card {
    max-width: 262.5px;
}

.card-img-top {
    margin-top: 10px;
    max-height: 300px;
    max-width: 300px;
}

